/* --- SOCIAL MEDIA ICONS --- */

footer .social-media-links ul {
    display: flex;
    justify-content: flex-start;

    padding: 0;
    margin: 0;
}

footer .social-media-links li {
    list-style-type: none;
    margin-right: 1.0rem;
}

footer .social-media-links img {
    width: 30px;
    height: 30px;
    margin: 0;
    padding: 10%;
    border-radius: 50%;
    filter: invert(100%);
}

.social-media-links img:hover {
    filter: invert(0%);
}



/* --- LAYOUT --- */

footer p {
    margin: 0;
    padding: 0;
}

.container {
    display: grid;
    grid-template-columns: 1fr 2fr;

    margin-top: 5%;
}

.item {
    text-align: left;
    vertical-align: top;
    padding-left: 10px;
}

.item-logo {
    text-align: right;
    grid-row: span 4;
}



/* --- TEXT --- */

.factotum-red {
    color: rgb(193, 72, 67);
}

.text {
    color: inherit;
    font-size: medium;
}

.year {
    font-size: medium;
}



@media screen and (max-width: 730px) {
    footer .social-media-links img {
        width: 20px;
        height: 20px;
    }

    .text {
        font-size: smaller;
    }
    
    .year {
        font-size: small;
    }
}